
.line-through {
  text-decoration: line-through;
  user-select: none;
}
a:link {
  color: #636363;
  text-decoration: none;
}
a:visited {
  color: #636363;
}
a:focus {
  color: #636363;
}
a:hover {
  color: #636363;
  text-decoration: underline;
  -webkit-text-decoration-skip: edges;
  text-decoration-skip: edges;
  -webkit-text-decoration-color: #FF001F;
  text-decoration-color: #FF001F;
}
a:active {
  color: #636363;
}
table {
  text-align: left;
  table-layout: fixed;
  width: 100%;
  white-space: nowrap;
}
caption {
  font-size: 1.6em;
  font-family: Roboto Mono, monospace;
  margin-bottom: 0.78125em;
  text-align: left;
}
th {
  font-size: 1.4em;
  font-family: Roboto Mono, monospace;
  margin-bottom: 0.44643em;
  font-weight: 500;
  padding: 0 10px 0 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  word-wrap: break-word;
}
td {
  font-size: 1.4em;
  font-family: Roboto Mono, monospace;
  margin-bottom: 0.44643em;
  padding: 0 10px 0 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  word-wrap: break-word;
}
