
.line-through {
  text-decoration: line-through;
  user-select: none;
}
.addUser h2 ~ p {
  line-height: 1.5em;
}
.addUser form {
  display: grid;
  grid-template-columns: 400px 1fr;
  grid-gap: 16px;
}
.addUser label {
  grid-column: 1 / 2;
  font-size: 1.4em;
  font-family: Roboto Mono, monospace;
  margin-bottom: 0;
}
.addUser input {
  grid-column: 2 / 3;
  font-size: 1.4em;
  font-family: Roboto Mono, monospace;
  margin-bottom: 0;
}
.addUser input:not([type=submit]) {
    padding: 10px;
}
.addUser input[type=submit] {
  width: 200px;
  background: none;
}
.addUser input[type=submit]:hover {
    background: #dedede;
}
.addUser .error {
  font-size: 1.4em;
  font-family: Roboto Mono, monospace;
  margin-bottom: 1.78571em;
  color: #FF0000;
}
