
.line-through {
  text-decoration: line-through;
  user-select: none;
}
.navHeader {
  display: grid;
  grid-template-columns: 50% 50%;
  grid-gap: 0;
}
.navLeftColumn {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.navRightColumn {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.navRightColumn > p {
    padding: 0 20px 0 0;
}
.navRightColumn > a:first-of-type p {
    padding: 0 20px 0 0;
}
